import React from 'react';
import windowSize from 'react-window-size';
import Confetti from 'react-confetti';

class UDConfetti extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hidden: false
    }
  }
  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          hidden: this.state.hidden,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
    else if (event.type === "clearElement") {
      this.setState({
        selectedOption: null
      });
    }
    else if (event.type === "removeElement") {
      this.setState({
        hidden: true
      });
    }
  }

  render() {
    if (this.state.hidden) {
      return null;
    }
    return (
      <Confetti
        width={this.props.windowWidth}
        height={this.props.windowHeight}
        numberOfPieces={this.props.numberOfPieces}
        wind={this.props.wind}
        gravity={this.props.gravity}
        initialVelocityX={this.props.initialVelocityX}
        initialVelocityY={this.props.initialVelocityY}
        colors={this.props.colors}
        opacity={this.props.opacity}
        recycle={this.props.recycle}
        run={this.props.run}
        tweenDuration={this.props.tweenDuration}
      />
    )

  }
}

export default UDConfetti
